import { Input, Select, Stack, Button } from "@chakra-ui/react";
import { useForm, useFieldArray } from "react-hook-form";
import { UserCommon, Student, Mentor } from "./userType";
import { FormInput } from "./FormInput";

export type StudentFormData = UserCommon & Student;
export type MentorFormData = UserCommon & Mentor;

export const UserForm: React.FC<{ onSubmit: (data: StudentFormData | MentorFormData) => void }> = ({ onSubmit }) => {
  const { register, handleSubmit, control, watch } = useForm<StudentFormData | MentorFormData>(
    { defaultValues: { role: "student" } }
  );
  // @ts-ignore
  const hobbiesFields = useFieldArray({ control, name: "hobbies" });
  // @ts-ignore
  const studyLangsFields = useFieldArray({ control, name: "studyLangs" });
  // @ts-ignore
  const useLangsFields = useFieldArray({ control, name: "useLangs" });

  const role = watch("role");

  return (
    <Stack as="form" onSubmit={handleSubmit(onSubmit)}>
      <FormInput label="" register={register("name")} type="text"/>
      <label htmlFor="role"></label>
      <Select {...register("role")}>
        <option value="student"></option>
        <option value="mentor"></option>
      </Select>
      <FormInput label="" register={register("email")} type="text"/>
      <FormInput label="" register={register("age")} type="number"/>
      <FormInput label="" register={register("postCode")} type="text"/>
      <FormInput label="" register={register("phone")} type="text"/>
      <FormInput label="URL" register={register("url")} type="text"/>

      <label htmlFor="hobbies"></label>
      <Stack spacing={4}>
        {hobbiesFields.fields.map((field, index) => (
          <Input key={field.id} {...register(`hobbies.${index}`)} type="text"/>
        ))}
        <Button onClick={() => hobbiesFields.append("")}></Button>
      </Stack>

      {role === "student" && (
        <>
          <FormInput label="" register={register("studyMinutes")} type="number"/>
          <FormInput label="" register={register("taskCode")} type="number"/>
          <FormInput label="" register={register("score")} type="number"/>

          <label htmlFor="hobbies"></label>
          <Stack spacing={4}>
            {studyLangsFields.fields.map((field, index) => (
              <Input key={field.id} {...register(`studyLangs.${index}`)} type="text"/>
            ))}
            <Button onClick={() => studyLangsFields.append("")}></Button>
          </Stack>
        </>
      )}

      {role === "mentor" && (
        <>
          <FormInput label="" register={register("experienceDays")} type="number"/>
          <FormInput label="" register={register("availableStartCode")} type="number"/>
          <FormInput label="" register={register("availableEndCode")} type="number"/>

          <label htmlFor="hobbies"></label>
          <Stack spacing={4}>
            {useLangsFields.fields.map((field, index) => (
              <Input key={field.id} {...register(`useLangs.${index}`)} type="text"/>
            ))}
            <Button onClick={() => useLangsFields.append({})}></Button>
          </Stack>
        </>
      )}

      <Input type="submit" />
    </Stack>
  );
};
